.daterangepicker {
    background-color: $drp-bg-color;

    td.in-range {
        background-color: $drp-td-in-range-bg-color;
        color: $drp-td-in-range-color;
    }

    td.off, td.off.in-range, td.off.start-date, td.off.end-date {
        background-color: $drp-calendar-bg-color;
    }

    td.available:hover, th.available:hover {
        background-color: $drp-available-hover-bg-color;
    }

    td.active, td.active:hover {
        background-color: $drp-td-active-bg-color;
        color: $drp-td-active-color;
    }

    .calendar-table {
        background-color: $drp-calendar-bg-color;
        border-color: $drp-calendar-border-color;
    }

    .ranges {
        li:hover {
            background-color: $drp-ranges-hover-bg-color;
        }

        li.active {
            background-color: $drp-td-active-bg-color;
        }
    }

    .drp-buttons {
        .btn.btn-primary {
            background-color: $drp-btn-bg-color;
        }
    }
}


